import Login from '../components/Login' // 登录
import Layout from '../components/Layout' 
import Home from '../components/Home' // 主页
import Administration from '../components/Administration' // 员工管理
import Archives from '../components/Administration/archives'  // 员工管理 > 档案管理
import Distribution from '../components/Administration/distribution' // 员工管理 > 分配管理
import Curriculum from '../components/Administration/curriculum' // 员工管理 > 课程管理 
import Substitute from '../components/Administration/substitute' // 员工管理 > 代课费管理 
import Checkwork from '../components/Checkwork' // 员工考核
import Workplan from '../components/Workplan' // 工作计划
import Customer from '../components/Customer' // 客户管理
import College from '../components/College' // 亚太学院
import Assessment from '../components/Assessment' // 级别考核
import Assistant from '../components/Assistant' // 管理助手
import Integral from '../components/Integral' // 积分管理

export default [
    {
        path: '/login', // 登陆
        component: Login
    },
    {
        path: '/layout',
        component: Layout,
        children: [
            {
                path: '/home', // 主页
                component: Home
            },
            {
                path: '/administration', // 员工管理
                component: Administration,
                children: [
                    {
                        path: '/archives', // 档案管理
                        component: Archives
                    },
                    {
                        path: '/distribution',  // 分配管理
                        component: Distribution
                    },
                    {
                        path: '/curriculum',  // 课程管理
                        component: Curriculum
                    },
                    {
                        path: '/substitute',
                        component: Substitute // 代课费管理
                    }
                ]
            },
            {
                path: '/checkwork', // 员工考核
                component: Checkwork
            },
            {
                path: '/workplan', // 工作计划
                component: Workplan
            },
            {
                path: '/customer', // 客户管理
                component: Customer
            },
            {
                path: '/college', // 亚太学院
                component: College
            },
            {
                path: '/assessment', // 级别考核
                component: Assessment
            },
            {
                path: '/assistant', // 管理助手
                component: Assistant
            },
            {
                path: '/integral', // 积分管理
                component: Integral
            }
        ]
    }
]