import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import '../style/aside.css'

export default class Aside extends Component {
    constructor (props) {
        super(props)
        this.state = {
            asideIndex: 0
        }
    }
    render() {
        let { asideList, path } = this.props
        let { asideIndex } = this.state
        return (
        <div className="aside">
            {
                asideList.map((item, index) => {
                    return <Link to={`${path}${item.path}`} key={index} onClick={() => {
                        this.asideClick(item, index)
                    }} className={asideIndex === index ? 'active' : ''}>{item.text}</Link>
                })
            }
        </div>
        )
    }
    asideClick (item, index) {
        this.setState({
            asideIndex: index
        })
    }
}

Aside.defaultProps = {
    asideList: [
        {
            text: '档案管理',
            path: '/archives'
        }, {
            text: '分配管理',
            path: '/distribution'
        }, {
            text: '课程管理',
            path: '/curriculum'
        }, {
            text: '代课费管理',
            path: '/substitute'
        }, {
            text: '生日提醒',
            path: '/birthday'
        }, {
            text: '薪酬管理',
            path: '/payment'
        }, {
            text: '离职管理',
            path: '/leaveoffice'
        }
    ]
}