import React, { Component } from 'react'
import { hash } from '../util'
import { Badge, Icon } from 'antd'
import { Link, withRouter } from 'react-router-dom'
import '../style/headerNav.css'

class HeaderNav extends Component {
    constructor (props) {
        super(props)
        this.state = {
            navIndex: 0
        }
    }
    render () {
        let { headerNavList, logoSrc, userName, msgNum, path, history } = this.props
        let { navIndex } = this.state
        return <div className="header">
                <h1 className="logo">
                    <a href="###">
                        <img src={logoSrc} alt=""/>
                    </a>
                </h1>
                <div className="headerNav">
                    {
                        headerNavList.map((item, index) => {
                            return <Link to={`${path}${item.path}`} key={hash()} onClick={() => {
                                this.headerNavClick(item, index)
                            }} className={index === navIndex ? 'active' : ''}>{item.text}</Link>
                        })
                    }
                </div>
                <div className="userMsg">
                    <div className="user">{userName}<span onClick={() => this.toLogin(history)}>退出</span></div>
                    <div className="msg">
                        <a href="###">
                            <Badge count={msgNum}>
                                <Icon type="mail" style={{fontSize: 25+'px', color: '#fff'}}/>
                            </Badge>
                            <span className="text">通知栏</span>
                        </a>
                    </div>
                </div>
            </div>
    }
    headerNavClick (item, index) {
        this.props.headerNavActive(item, index)
        this.setState({
            navIndex: index
        })
    }
    toLogin (history) {
        localStorage.removeItem('user')
        history.push('/login')
    }
}

HeaderNav.defaultProps = {
    headerNavList: [
        { text: '首页', path: '/home' },
        { text: '员工管理', path: '/administration' },
        { text: '员工考勤', path: '/checkwork' },
        { text: '工作计划', path: '/workplan' },
        { text: '客户管理', path: '/customer' },
        { text: '亚太学院', path: '/college' },
        { text: '级别考核', path: '/assessment' },
        { text: '管理助手', path: '/assistant' },
        { text: '积分管理', path: '/integral' }
    ],
    headerNavActive: (item, index) => {
        console.log(item, index)
    },
    logoSrc: '/image/logo.png',
    userName: 'wangqi',
    msgNum: 5,
    path: ''
}

export default withRouter(HeaderNav)