import { Route, Switch, withRouter, Redirect } from 'react-router-dom'
import React from 'react'

function RenderRouter({routes, match}) {
    return <Switch>
        {
            routes.map((item, index) => {
                let url = match.path === '/' ? item.path : match.path+item.path
                return <Route key={index} path={url} render={ (props) => {
                    let Child = item.component;
                    return <Child {...props} routes={item.children}/>
                }} />
            })
        }
        <Redirect from="/" to="/login" exact/>
        <Redirect from="/layout/administration" to="/layout/administration/archives" exact/>
    </Switch> 
}
export default withRouter(RenderRouter)