import React, { Component } from 'react'

export default class Card extends Component {
  render() {
    let { cardData, children } = this.props
    return (
      <div className="card">
        <p className="card_top">
          <span className="title">{cardData.title}</span>
        </p>
        {children}
      </div>
    )
  }
}
