import React, { Component } from 'react'

export default class UserInfo extends Component {
  render() {
    let { userInfo, title, getNewTitle } = this.props
    return (
      <div className="user_info">
        <div className="userHead">
            <img src={userInfo.userHead} alt=""/>
        </div>
        <p className="userName">Hi,{userInfo.userName}</p>
        <p>岗位:{userInfo.post}</p>
        <p>总积分:{userInfo.integral}</p>
        <p>积分排名:{userInfo.ranking}</p>
        <p onClick={() => getNewTitle({title: '你胆真大'})}>{title}</p>
      </div>
    )
  }
}
